import api from "./axiosInstance";

import { TRANSFER_QUERY } from "./queries";
import USD from '../schemas/usd.js';
// import { response_transfer } from "./_response.js";

/*
Excelente pregunta. Hay diferencias importantes entre Transferencias (Transfers) y Transacciones (Transactions) en el contexto de blockchain:

Transacciones (Transactions)

Son la unidad fundamental de cuenta en una blockchain.
Representan cualquier operacin que modifica el estado de la blockchain.
Cada transaccin tiene un hash nico, un remitente (from), posiblemente un destinatario (to), y datos de entrada.
Incluyen transacciones de transferencia de valor, llamadas a contratos inteligentes, despliegue de contratos, etc.
Siempre se registran directamente en la blockchain.
Transferencias (Transfers)

Son un subconjunto especfico de actividad que implica el movimiento de valor (tokens o criptomonedas).
Pueden ocurrir como parte de una transaccin directa o como resultado de la ejecucin de un contrato inteligente.
Incluyen transferencias de ETH, tokens ERC-20, NFTs, etc.
Una sola transaccin puede contener mltiples transferencias.
Las transferencias pueden ser "internas" cuando ocurren dentro de la ejecucin de un contrato inteligente.
Ejemplo prctico:

Si llama a un contrato inteligente que distribuye tokens a 5 direcciones diferentes, esto se registrar como:

1 transaccin (su llamada al contrato)
5 transferencias (los movimientos de tokens a cada destinatario)
En la API de Bitquery, Transfers se enfoca especficamente en los movimientos de valor, mientras que Transactions proporciona informacin sobre las operaciones completas en la blockchain.

Le gustara ver tambin una consulta para las transacciones de esta direccin?
*/

export const getTransfers = async (params, chain) => {
  const query = TRANSFER_QUERY({...params, chain: chain.getInfo().chain});
  const data = JSON.stringify({ query });

  const Usd = new USD();

  try {
    const response = await api.post(data); 
    // const response = response_transfer;
    console.log('response', response);

    const { Transfers } = response.data.EVM;
    const parsed = Transfers.map((transfer) => {
      const { Transaction, Transfer } = transfer;
      return{
        hash: { hash: Transaction.Hash, link: chain.getInfo().txWeb },
        date: Transaction.Time,
        from: { hash: Transfer.Sender, link: chain.getInfo().walletWeb },
        to: { hash: Transfer.Receiver, link: chain.getInfo().walletWeb },
        gas: Transaction.Gas,
        value: {
          value: Transfer.Amount,
          chain: chain.getInfo(),
        },
        valueUSD: {
          value: Transfer.AmountInUSD,
          chain: Usd.getInfo(),
        },
        priceUSD: {
          value: Transfer.Amount/Transfer.AmountInUSD,
          chain: Usd.getInfo(),
        },
        cost: {
          value: Transaction.Cost,
          chain: chain.getInfo(),
        },
        costUSD: {
          value: Transaction.CostInUSD,
          chain: Usd.getInfo(),
        },
        status: Transfer.Success,
        type: Transfer.Type,
      }
    });

    return parsed;
  } catch (error) {
    console.error("Error fetching transactions", error);
    return [];
  }
};